import React from 'react';
interface Step {
  image: string;
  description: string;
}

interface Props {
  steps: Step[];
  title: string;
}

const StepCard = ({ image, description }: Step) => (
  <div data-sal="fade" data-sal-delay="100">
    <div className="p-8 md:flex md:items-center">
      <img className="w-48 mx-auto p-2" src={image.publicURL} alt={image.name} loading="lazy" />
      <p className="text-justify md:px-8">{description}</p>
    </div>
  </div>
);

const Concept = ({ steps, title }: Props) => (
  <div className="text-blue p-8 py-20 md:p-20 max-w-4xl mx-auto">
    <h2 className="text-center font-brand pb-4 md:pb-12">{title}</h2>
    {steps.map(step => (
      <StepCard image={step.image} description={step.description} />
    ))}
  </div>
);

export default Concept;
